@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:400,600,700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font-family:'Nunito', sans-serif;vertical-align:baseline;font-family:'Open Sans', sans-serif}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}p{line-height:1.4}

:root{--black: black;--white: white}.appear{-webkit-animation-name:appear;animation-name:appear;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-iteration-count:initial;animation-iteration-count:initial}@-webkit-keyframes appear{0%{opacity:0;transform:translateY(10px)}100%{opacity:1;transform:translateY(0)}}@keyframes appear{0%{opacity:0;transform:translateY(10px)}100%{opacity:1;transform:translateY(0)}}.loader{border:10px solid #f3f3f3;border-top:10px solid #3498db;border-radius:50%;width:50px;height:50px;-webkit-animation:spin 2s linear infinite;animation:spin 2s linear infinite;margin-bottom:50px}@-webkit-keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.positive{border:solid 3px #1aad06}.negative{border:solid 3px #ee0c0c}.neutro{border:solid 3px #eedf0c}

.Home{width:100vw;height:calc(100vh - 16px);display:flex;flex-direction:column;justify-content:center;align-items:center}.Home__content{background:rgba(0,0,0,0.7);width:100%;height:calc(100vh - 16px);position:absolute;padding:50px;box-sizing:border-box;top:0;z-index:10;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.Home__content__body{color:var(--white);text-align:center;display:flex;flex-direction:column;justify-content:center;align-items:center}.Home__content__body__title{font-size:40px;margin-bottom:15px}.Home__content__body__text{max-width:700px;margin:50px 0}.Home__content__body__btn{display:flex;justify-content:center;align-items:center;width:200px;height:40px;background-color:var(--white);color:var(--black);text-decoration:none;font-size:20px;font-weight:bolder;border-radius:5px}

.Logo{display:block;width:-webkit-max-content;width:-moz-max-content;width:max-content;border-bottom:solid 5px var(--white);text-decoration:none;padding-bottom:10px;box-sizing:border-box}.Logo h1{font-weight:bolder;color:var(--white);font-size:40px}

.Countries{width:100%;height:100%}

.Morning{width:100vw;height:calc( 100vh - 16px)}.Morning__background{width:100%;height:100%;background-size:contain;background-repeat:no-repeat;background-position:center}

.Datacard{width:250px;position:absolute;z-index:2;right:40px;top:40px}.Datacard__body{background-color:var(--white);min-height:380px;padding:30px;box-sizing:border-box;border-radius:11px;box-shadow:0px 0px 4px rgba(105,105,105,0.25);display:flex;flex-direction:column;justify-content:space-between;align-items:center}.Datacard__body__flag{width:90px;height:90px;border-radius:90px;border:solid 5px var(--white);background-size:cover;background-repeat:no-repeat;background-position:center;box-shadow:0px 0px 4px rgba(105,105,105,0.25)}.Datacard__body__state{width:100%;text-align:center;border-radius:11px;box-shadow:0px 0px 4px rgba(105,105,105,0.25);overflow:hidden;margin:20px 0}.Datacard__body__state__header{padding:10px;box-sizing:border-box}.Datacard__body__state__body{padding:15px 10px;box-sizing:border-box;font-size:14px}.Datacard__body__hour{width:100%;display:flex;justify-content:space-around;align-items:center}.Datacard__body__hour__text{text-align:center}.Datacard__btn{display:block;background-color:var(--white);box-shadow:0px 0px 4px rgba(105,105,105,0.25);border-radius:11px;color:var(--black);font-weight:bolder;font-size:14px;text-align:center;text-decoration:none;text-transform:uppercase;margin:15px 0;padding:10px 20px;box-sizing:border-box;transition:all .4s}.Datacard__btn:hover{background-color:#d3d3d3}

.Noon{width:100vw;height:calc( 100vh - 16px)}.Noon__background{width:100%;height:100%;background-size:contain;background-repeat:no-repeat;background-position:center}

.Night{width:100vw;height:calc( 100vh - 16px)}.Night__background{width:100%;height:100%;background-size:cover;background-position:center;background-repeat:no-repeat}

.AfterNoon{width:100vw;height:calc( 100vh - 16px)}.AfterNoon__background{width:100%;height:100%;background-size:contain;background-repeat:no-repeat;background-position:center}

.Credits{background:var(--black);display:flex;justify-content:center;align-items:center;color:var(--white);font-size:12px}.Credits a{margin-left:5px;color:var(--white)}

.Countries{width:100%;height:100%}.Countries__loading{width:100vw;height:calc( 100vh - 16px);display:flex;flex-direction:column;justify-content:center;align-items:center}

.Countries{width:100%;height:100%}

